export default {
    data(){
        return {
            account: '',
            pass: '12345678'
        }
    },
    methods:{
        login() {
        if (!this.account)
            return this.$vux.toast.show({
                text: '请输入帐号',
                type: 'text',
                position: 'top'
            });
        if (!this.pass)
            return this.$vux.toast.show({
                text: '请输入密码',
                type: 'text',
                position: 'top'
            });
        this.$store.dispatch('login', {
                name: this.account,
                password: this.pass,
                openId: this.$route.query.openId || undefined,
                userType : 4
            })
            .then(
                result =>{
                    if(result.status){
                        var roleId = result.entry.roleId;
                        this.$router.push({
                            path: '/main' + roleId
                        })
                    }else{
                        this.$vux.toast.show({
                            text: result.message,
                            width: '18em',
                            type: 'text',
                            position: 'top'
                        })
                    }
                }
            )
        },
        keyUpReturn()
        {
            this.login();
        }
    },
    mounted(){

    }
}



